import React, { useState, useEffect } from "react";
import Navbar from "../../Navbar/Navbar";
import WizardParentRedux from "../../Wizard/WizardParentRedux";
import { GApageView, Event } from "../../Tracking/index";
import "./Adoptera.css";

const Adoptera = () => {
  const [showQuiz, setShowQuiz] = useState(false);
  const [showHeader, setShowHeader] = useState(true);

  useEffect(() => {
    GApageView("adoptera");
  }, []);

  const startQuiz = () => {
    setShowQuiz(true);
    setShowHeader(false);
    Event("QUIZ", "Startade quiz", "QUIZ-startat");
  };
  return (
    <>
      <Navbar black />
      {showHeader && (
        <div className="headerWizard">
          {/* <h2>Hitta din <br /> pawfect match!</h2> */}
          <h2>Låt vårt matchningsverktyg <br /> hitta din <br /> pawfect match!</h2>
          {/* <h2>Använd vårt matchningsverktyg för att hitta din pawfect match</h2> */}
          {/* <p className="headerWizardHeading">
            Låt vårt matchningsverktyg identifiera lämpliga matchningar till
            dig.
          </p>
          <p className="headerWizardDisclaimer">
            Psst! Var noga med att svara sanningsenligt på testet, för dig och
            din framtida hunds skull.
          </p> */}
          <button onClick={startQuiz} className="button">
            Starta testet
          </button>
        </div>
      )}
      {showQuiz && <WizardParentRedux />}
    </>
  );
};

export default Adoptera;
